<template>
    <div>
        <div class="info_dev">
			<div class="info_dev_text">
				<p>The work was performed by Yuri Batalin and Egor Mitrofanov</p>
				Variant 81327
			</div>
		</div>
        <div class="login_side">
            <form class="form_log" @submit.prevent="submitHandler" method="post">
                <div id="info_log">
                    <a id="text">
                        Account Login
                    </a>
                    <div class="input_par">
                        <input
                            class="input"
                            type="text"
                            name="name"
                            id="name"
                            placeholder="Name"
                            v-model.trim="userName"
                            :class="{invalid: ($v.userName.$dirty && !$v.userName.required)}"
                        >
                        <div>
                            <small
                                class="errorText"
                                v-if="$v.userName.$dirty && !$v.userName.required">
                                    Поле не должно быть пустым
                            </small>
                        </div>
                    </div>
                    <div class="input_par">
                        <input 
                            class="input"
                            type="password"
                            name="pass"
                            id="pass"
                            placeholder="Password"
                            v-model.trim="pass"
                            :class="{invalid: ($v.pass.$dirty && !$v.pass.required) || ($v.pass.$dirty && !$v.pass.minLength)}"
                        >
                        <div>
                            <small
                                class="errorText"
                                v-if="$v.pass.$dirty && !$v.pass.required">
                                Введите пароль
                            </small>
                        </div>
                        <div>
                            <small
                                class="errorText"
                                v-if="flagLogin == 'false'">
                                    Неверные данные для входа
                            </small>
                        </div>
                    </div>
                    <div class="input_par">
                        <button class="btn_in" type="submit">
                            Sign in
                        </button>
                    </div>
                    <div class="input_par">
                        <a>
                            Create an accaunt?
                        </a>
                        <router-link to="/reg">Sign up</router-link>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import {required, minLength} from 'vuelidate/lib/validators'
import messages from '@/utils/messages'
import axios from 'axios'
import Vue from 'vue'


export default {
    name: 'login',
    data:() => ({
        userName: '',
        pass: '',
        flagLogin: null
    }),
    validations:{
        userName: {required},
        pass: {required, minLength: minLength(5)}
    },
    mounted(){
        if(messages[this.$route.query.message]){
            this.$message(messages[this.$route.query.message])
        }
    },
    methods: {
      submitHandler: function () {
        if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
        const formData = {
          userName: this.userName,
          pass: this.pass
        }
        axios.post('http://localhost:8080/rest/login', formData).then(function (response) {
          if (response.data.toString().split(";")[0] == "true") {
            Vue.prototype.token = response.data.toString().split(";")[1];
            this.$router.push('/main')
          }
        }).catch(function (error) {
          console.log(error);
        });
        console.log(this.flagLogin + ' - flag ' + Vue.prototype.token + ' - token')
      }
    }
}
</script>